/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --header:			linear-gradient(#edeced, #d2d1d2) no-repeat border-box;
  --header-border:	#b7b4b7;
  --text:				rgba(0,0,0,.73);
  --back: 			#ECECEC;
  --line:				rgba(0,0,0,.15);
  --window:			rgb(240,239,240);
  --modal:			rgba(240,239,240,.95);
  --green:			#009B33;
  --purple:			#8100FF;
}

[data-theme^="dark"] {
  --header:			linear-gradient(#4b4b4b, #3d3d3d) no-repeat border-box;
  --header-border:	#000000;
  --text:				rgba(255,255,255,.85);
  --back:				rgb(50, 51, 53);
  --line:				rgba(255,255,255,.15);
  --window:			rgb(48,48,48);
  --modal:			rgba(48,48,48,.95);
  --green:			#01F952;
  --purple:			#D02FFF;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text);
}

body {
  text-align: center;
  padding: 0 40px;
  background-color: var(--window);
}

body header {
  height: 30px;
  -webkit-app-region: drag;
}

body main {
  padding-bottom: 20px;
}

body main figure {
  width: 128px;
  height: 128px;
  margin: 10px auto 20px;
}

body main figure img {
  display: block;
}

body main h1 {
  font-size: 14px;
  margin-bottom: 10px;
}

body main h1 #version {
  font-size: 11px;
  color: var(--text);
  font-weight: 400;
  padding-left: 10px;
}

body main p {
  font-size: 11px;
  font-weight: 400;
  color: var(--text);
}

body footer table {
  margin: 0 auto;
  font-size: 10px;
  line-height: 14px;
  font-weight: 400;
  color: var(--text);
}

body footer table th,
body footer table td {
  padding: 0 4px;
}

body footer table th {
  text-align: right;
}

body footer table td {
  text-align: left;
}

body footer table tfoot button {
  display: block;
  width: auto;
  height: 21px;
  line-height: 11px;
  margin: 10px auto 0;
}

:focus {
  outline: none;
}

.button {
  border-radius: 4px;
  font-size: 13px;
  line-height: 13px;
  color: var(--text);
  font-weight: 500;
  text-align: center;
}

.button[disabled] {
  opacity: .5;
}

.button.focus, .button:focus {
  background-image: linear-gradient(180deg, #6CB3FA 0%, #067DFF 100%);
  color: #fff;
  border-color: #4FA3F9 #4FA3F9 #015DFF;
  outline: none;
}

.button:active:not([disabled]) {
  background-image: linear-gradient(180deg, #4C91FF 0%, #045DE4 100%);
  color: #fff;
  border-color: #2479FF #1161EC #0050DC;
}

[data-theme^="dark"] .button {
  background-image: none;
  background: rgba(255, 255, 255, 0.25);
  border: rgba(0, 0, 0, 0.35);
  box-shadow: inset 0 1px 1px rgba(255, 255, 255, 0.16), inset 0 1px 1px rgba(255, 255, 255, 0.4), 0 1px 1.5px rgba(0, 0, 0, 0.2);
}

[data-theme^="dark"] .button.focus, [data-theme^="dark"] .button:focus {
  background-image: linear-gradient(180deg, #1768E5 0%, #145CCC 100%);
  border: 0;
  box-shadow: inset 0 1px 1px rgba(255, 255, 255, 0.2), 0 1px 1px rgba(0, 0, 0, 0.4);
}

[data-theme^="dark"] .button:active:not([disabled]) {
  background-image: linear-gradient(180deg, #247BF4 0%, #1F72E6 100%);
  box-shadow: inset 0 1px 1px rgba(255, 255, 255, 0.2), 0 1px 1px rgba(0, 0, 0, 0.4);
}
